html[dir="rtl"] body {
  text-align: right; }
  html[dir="rtl"] body * {
    letter-spacing: 0; }
  html[dir="rtl"] body .home-banner .banner_cap {
    left: auto;
    right: 12%; }
  html[dir="rtl"] body .btn_wrap a span {
    transform: scaleX(-1); }
  html[dir="rtl"] body .comunity_sec .center_div .text_ .content_ {
    left: auto;
    right: -27%; }
  html[dir="rtl"] body .comunity_sec .center_div:nth-of-type(4) .text_ .content_ {
    left: -27%;
    right: auto; }
  html[dir="rtl"] body .comunity_sec .lf_div .text_ .content_ {
    left: auto;
    right: 15%; }
  html[dir="rtl"] body .comunity_sec .rgt_div {
    float: left; }
    html[dir="rtl"] body .comunity_sec .rgt_div .text_ .content_ {
      left: auto;
      right: -30%; }
  html[dir="rtl"] body .keep_know .kn_img {
    right: auto;
    left: 0; }
  html[dir="rtl"] body .keep_know .inner_ .lf_ .subscrib_bx .btn {
    transform: scaleX(-1); }
  html[dir="rtl"] body .inspire_sec .inner_ .insp_box_ .involed_wraper .box_ .bx_btm .rgster span {
